.elements {
    max-width: 882px;
    margin: 0 auto;
    display: grid;
    list-style: none;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    grid-column-gap: 18px;
    grid-row-gap: 20px;
    padding-left: 19px;
    padding-right: 19px;
    justify-content: center;
}