.popup {
    position: fixed;
    background-color: rgba(0, 0, 0, .5);
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    display: flex;
    top: 0;
    right: 0;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: visibility .2s linear , opacity .2s linear;
}