.footer__copyright {
    margin-top: 0;
    margin-bottom: 0;
    color: #545454;
    font-size: 18px;
    line-height: calc(22/18);
    font-weight: normal;
}

@media screen and (max-width: 700px) {
    .footer__copyright {
        margin-left: 27px;
        margin-bottom: 20px;
        font-size: 14px;
        line-height: calc(17/14);
    }
}